___CSS_LOADER_EXPORT___.push([module.id, ".action-link[data-v-1a6c22aa]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/passport/AuthorizedClients.vue"],"names":[],"mappings":"AACA,8BACA,cACA","sourcesContent":["<style scoped>\r\n    .action-link {\r\n        cursor: pointer;\r\n    }\r\n</style>\r\n\r\n<template>\r\n    <div>\r\n        <div v-if=\"tokens.length > 0\">\r\n            <div class=\"card card-default\">\r\n                <div class=\"card-header\">Authorized Applications</div>\r\n\r\n                <div class=\"card-body\">\r\n                    <!-- Authorized Tokens -->\r\n                    <table class=\"table table-borderless mb-0\">\r\n                        <thead>\r\n                            <tr>\r\n                                <th>Name</th>\r\n                                <th>Scopes</th>\r\n                                <th></th>\r\n                            </tr>\r\n                        </thead>\r\n\r\n                        <tbody>\r\n                            <tr v-for=\"token in tokens\">\r\n                                <!-- Client Name -->\r\n                                <td style=\"vertical-align: middle;\">\r\n                                    {{ token.client.name }}\r\n                                </td>\r\n\r\n                                <!-- Scopes -->\r\n                                <td style=\"vertical-align: middle;\">\r\n                                    <span v-if=\"token.scopes.length > 0\">\r\n                                        {{ token.scopes.join(', ') }}\r\n                                    </span>\r\n                                </td>\r\n\r\n                                <!-- Revoke Button -->\r\n                                <td style=\"vertical-align: middle;\">\r\n                                    <a class=\"action-link text-danger\" @click=\"revoke(token)\">\r\n                                        Revoke\r\n                                    </a>\r\n                                </td>\r\n                            </tr>\r\n                        </tbody>\r\n                    </table>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    export default {\r\n        /*\r\n         * The component's data.\r\n         */\r\n        data() {\r\n            return {\r\n                tokens: []\r\n            };\r\n        },\r\n\r\n        /**\r\n         * Prepare the component (Vue 1.x).\r\n         */\r\n        ready() {\r\n            this.prepareComponent();\r\n        },\r\n\r\n        /**\r\n         * Prepare the component (Vue 2.x).\r\n         */\r\n        mounted() {\r\n            this.prepareComponent();\r\n        },\r\n\r\n        methods: {\r\n            /**\r\n             * Prepare the component (Vue 2.x).\r\n             */\r\n            prepareComponent() {\r\n                this.getTokens();\r\n            },\r\n\r\n            /**\r\n             * Get all of the authorized tokens for the user.\r\n             */\r\n            getTokens() {\r\n                axios.get('/oauth/tokens')\r\n                        .then(response => {\r\n                            this.tokens = response.data;\r\n                        });\r\n            },\r\n\r\n            /**\r\n             * Revoke the given token.\r\n             */\r\n            revoke(token) {\r\n                axios.delete('/oauth/tokens/' + token.id)\r\n                        .then(response => {\r\n                            this.getTokens();\r\n                        });\r\n            }\r\n        }\r\n    }\r\n</script>\r\n"],"sourceRoot":""}]);
