import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".action-link[data-v-00b77ae4]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/passport/Clients.vue"],"names":[],"mappings":"AACA,8BACA,cACA","sourcesContent":["<style scoped>\r\n    .action-link {\r\n        cursor: pointer;\r\n    }\r\n</style>\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"card card-default\">\r\n            <div class=\"card-header\">\r\n                <div style=\"display: flex; justify-content: space-between; align-items: center;\">\r\n                    <span>\r\n                        OAuth Clients\r\n                    </span>\r\n\r\n                    <a class=\"action-link\" tabindex=\"-1\" @click=\"showCreateClientForm\">\r\n                        Create New Client\r\n                    </a>\r\n                </div>\r\n            </div>\r\n\r\n            <div class=\"card-body\">\r\n                <!-- Current Clients -->\r\n                <p class=\"mb-0\" v-if=\"clients.length === 0\">\r\n                    You have not created any OAuth clients.\r\n                </p>\r\n\r\n                <table class=\"table table-borderless mb-0\" v-if=\"clients.length > 0\">\r\n                    <thead>\r\n                        <tr>\r\n                            <th>Client ID</th>\r\n                            <th>Name</th>\r\n                            <th>Secret</th>\r\n                            <th></th>\r\n                            <th></th>\r\n                        </tr>\r\n                    </thead>\r\n\r\n                    <tbody>\r\n                        <tr v-for=\"client in clients\">\r\n                            <!-- ID -->\r\n                            <td style=\"vertical-align: middle;\">\r\n                                {{ client.id }}\r\n                            </td>\r\n\r\n                            <!-- Name -->\r\n                            <td style=\"vertical-align: middle;\">\r\n                                {{ client.name }}\r\n                            </td>\r\n\r\n                            <!-- Secret -->\r\n                            <td style=\"vertical-align: middle;\">\r\n                                <code>{{ client.secret }}</code>\r\n                            </td>\r\n\r\n                            <!-- Edit Button -->\r\n                            <td style=\"vertical-align: middle;\">\r\n                                <a class=\"action-link\" tabindex=\"-1\" @click=\"edit(client)\">\r\n                                    Edit\r\n                                </a>\r\n                            </td>\r\n\r\n                            <!-- Delete Button -->\r\n                            <td style=\"vertical-align: middle;\">\r\n                                <a class=\"action-link text-danger\" @click=\"destroy(client)\">\r\n                                    Delete\r\n                                </a>\r\n                            </td>\r\n                        </tr>\r\n                    </tbody>\r\n                </table>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Create Client Modal -->\r\n        <div class=\"modal fade\" id=\"modal-create-client\" tabindex=\"-1\" role=\"dialog\">\r\n            <div class=\"modal-dialog\">\r\n                <div class=\"modal-content\">\r\n                    <div class=\"modal-header\">\r\n                        <h4 class=\"modal-title\">\r\n                            Create Client\r\n                        </h4>\r\n\r\n                        <button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-hidden=\"true\">&times;</button>\r\n                    </div>\r\n\r\n                    <div class=\"modal-body\">\r\n                        <!-- Form Errors -->\r\n                        <div class=\"alert alert-danger\" v-if=\"createForm.errors.length > 0\">\r\n                            <p class=\"mb-0\"><strong>Whoops!</strong> Something went wrong!</p>\r\n                            <br>\r\n                            <ul>\r\n                                <li v-for=\"error in createForm.errors\">\r\n                                    {{ error }}\r\n                                </li>\r\n                            </ul>\r\n                        </div>\r\n\r\n                        <!-- Create Client Form -->\r\n                        <form role=\"form\">\r\n                            <!-- Name -->\r\n                            <div class=\"form-group row\">\r\n                                <label class=\"col-md-3 col-form-label\">Name</label>\r\n\r\n                                <div class=\"col-md-9\">\r\n                                    <input id=\"create-client-name\" type=\"text\" class=\"form-control\"\r\n                                                                @keyup.enter=\"store\" v-model=\"createForm.name\">\r\n\r\n                                    <span class=\"form-text text-muted\">\r\n                                        Something your users will recognize and trust.\r\n                                    </span>\r\n                                </div>\r\n                            </div>\r\n\r\n                            <!-- Redirect URL -->\r\n                            <div class=\"form-group row\">\r\n                                <label class=\"col-md-3 col-form-label\">Redirect URL</label>\r\n\r\n                                <div class=\"col-md-9\">\r\n                                    <input type=\"text\" class=\"form-control\" name=\"redirect\"\r\n                                                    @keyup.enter=\"store\" v-model=\"createForm.redirect\">\r\n\r\n                                    <span class=\"form-text text-muted\">\r\n                                        Your application's authorization callback URL.\r\n                                    </span>\r\n                                </div>\r\n                            </div>\r\n\r\n                            <!-- Confidential -->\r\n                            <div class=\"form-group row\">\r\n                                <label class=\"col-md-3 col-form-label\">Confidential</label>\r\n\r\n                                <div class=\"col-md-9\">\r\n                                    <div class=\"checkbox\">\r\n                                        <label>\r\n                                            <input type=\"checkbox\" v-model=\"createForm.confidential\">\r\n                                        </label>\r\n                                    </div>\r\n\r\n                                    <span class=\"form-text text-muted\">\r\n                                        Require the client to authenticate with a secret. Confidential clients can hold credentials in a secure way without exposing them to unauthorized parties. Public applications, such as native desktop or JavaScript SPA applications, are unable to hold secrets securely.\r\n                                    </span>\r\n                                </div>\r\n                            </div>\r\n                        </form>\r\n                    </div>\r\n\r\n                    <!-- Modal Actions -->\r\n                    <div class=\"modal-footer\">\r\n                        <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Close</button>\r\n\r\n                        <button type=\"button\" class=\"btn btn-primary\" @click=\"store\">\r\n                            Create\r\n                        </button>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Edit Client Modal -->\r\n        <div class=\"modal fade\" id=\"modal-edit-client\" tabindex=\"-1\" role=\"dialog\">\r\n            <div class=\"modal-dialog\">\r\n                <div class=\"modal-content\">\r\n                    <div class=\"modal-header\">\r\n                        <h4 class=\"modal-title\">\r\n                            Edit Client\r\n                        </h4>\r\n\r\n                        <button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-hidden=\"true\">&times;</button>\r\n                    </div>\r\n\r\n                    <div class=\"modal-body\">\r\n                        <!-- Form Errors -->\r\n                        <div class=\"alert alert-danger\" v-if=\"editForm.errors.length > 0\">\r\n                            <p class=\"mb-0\"><strong>Whoops!</strong> Something went wrong!</p>\r\n                            <br>\r\n                            <ul>\r\n                                <li v-for=\"error in editForm.errors\">\r\n                                    {{ error }}\r\n                                </li>\r\n                            </ul>\r\n                        </div>\r\n\r\n                        <!-- Edit Client Form -->\r\n                        <form role=\"form\">\r\n                            <!-- Name -->\r\n                            <div class=\"form-group row\">\r\n                                <label class=\"col-md-3 col-form-label\">Name</label>\r\n\r\n                                <div class=\"col-md-9\">\r\n                                    <input id=\"edit-client-name\" type=\"text\" class=\"form-control\"\r\n                                                                @keyup.enter=\"update\" v-model=\"editForm.name\">\r\n\r\n                                    <span class=\"form-text text-muted\">\r\n                                        Something your users will recognize and trust.\r\n                                    </span>\r\n                                </div>\r\n                            </div>\r\n\r\n                            <!-- Redirect URL -->\r\n                            <div class=\"form-group row\">\r\n                                <label class=\"col-md-3 col-form-label\">Redirect URL</label>\r\n\r\n                                <div class=\"col-md-9\">\r\n                                    <input type=\"text\" class=\"form-control\" name=\"redirect\"\r\n                                                    @keyup.enter=\"update\" v-model=\"editForm.redirect\">\r\n\r\n                                    <span class=\"form-text text-muted\">\r\n                                        Your application's authorization callback URL.\r\n                                    </span>\r\n                                </div>\r\n                            </div>\r\n                        </form>\r\n                    </div>\r\n\r\n                    <!-- Modal Actions -->\r\n                    <div class=\"modal-footer\">\r\n                        <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Close</button>\r\n\r\n                        <button type=\"button\" class=\"btn btn-primary\" @click=\"update\">\r\n                            Save Changes\r\n                        </button>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    export default {\r\n        /*\r\n         * The component's data.\r\n         */\r\n        data() {\r\n            return {\r\n                clients: [],\r\n\r\n                createForm: {\r\n                    errors: [],\r\n                    name: '',\r\n                    redirect: '',\r\n                    confidential: true\r\n                },\r\n\r\n                editForm: {\r\n                    errors: [],\r\n                    name: '',\r\n                    redirect: ''\r\n                }\r\n            };\r\n        },\r\n\r\n        /**\r\n         * Prepare the component (Vue 1.x).\r\n         */\r\n        ready() {\r\n            this.prepareComponent();\r\n        },\r\n\r\n        /**\r\n         * Prepare the component (Vue 2.x).\r\n         */\r\n        mounted() {\r\n            this.prepareComponent();\r\n        },\r\n\r\n        methods: {\r\n            /**\r\n             * Prepare the component.\r\n             */\r\n            prepareComponent() {\r\n                this.getClients();\r\n\r\n                $('#modal-create-client').on('shown.bs.modal', () => {\r\n                    $('#create-client-name').focus();\r\n                });\r\n\r\n                $('#modal-edit-client').on('shown.bs.modal', () => {\r\n                    $('#edit-client-name').focus();\r\n                });\r\n            },\r\n\r\n            /**\r\n             * Get all of the OAuth clients for the user.\r\n             */\r\n            getClients() {\r\n                axios.get('/oauth/clients')\r\n                        .then(response => {\r\n                            this.clients = response.data;\r\n                        });\r\n            },\r\n\r\n            /**\r\n             * Show the form for creating new clients.\r\n             */\r\n            showCreateClientForm() {\r\n                $('#modal-create-client').modal('show');\r\n            },\r\n\r\n            /**\r\n             * Create a new OAuth client for the user.\r\n             */\r\n            store() {\r\n                this.persistClient(\r\n                    'post', '/oauth/clients',\r\n                    this.createForm, '#modal-create-client'\r\n                );\r\n            },\r\n\r\n            /**\r\n             * Edit the given client.\r\n             */\r\n            edit(client) {\r\n                this.editForm.id = client.id;\r\n                this.editForm.name = client.name;\r\n                this.editForm.redirect = client.redirect;\r\n\r\n                $('#modal-edit-client').modal('show');\r\n            },\r\n\r\n            /**\r\n             * Update the client being edited.\r\n             */\r\n            update() {\r\n                this.persistClient(\r\n                    'put', '/oauth/clients/' + this.editForm.id,\r\n                    this.editForm, '#modal-edit-client'\r\n                );\r\n            },\r\n\r\n            /**\r\n             * Persist the client to storage using the given form.\r\n             */\r\n            persistClient(method, uri, form, modal) {\r\n                form.errors = [];\r\n\r\n                axios[method](uri, form)\r\n                    .then(response => {\r\n                        this.getClients();\r\n\r\n                        form.name = '';\r\n                        form.redirect = '';\r\n                        form.errors = [];\r\n\r\n                        $(modal).modal('hide');\r\n                    })\r\n                    .catch(error => {\r\n                        if (typeof error.response.data === 'object') {\r\n                            form.errors = _.flatten(_.toArray(error.response.data.errors));\r\n                        } else {\r\n                            form.errors = ['Something went wrong. Please try again.'];\r\n                        }\r\n                    });\r\n            },\r\n\r\n            /**\r\n             * Destroy the given client.\r\n             */\r\n            destroy(client) {\r\n                axios.delete('/oauth/clients/' + client.id)\r\n                        .then(response => {\r\n                            this.getClients();\r\n                        });\r\n            }\r\n        }\r\n    }\r\n</script>\r\n"],"sourceRoot":""}]);
