// Imports
import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".action-link[data-v-608de639]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/components/passport/PersonalAccessTokens.vue"],"names":[],"mappings":"AACA,8BACA,cACA","sourcesContent":["<style scoped>\r\n    .action-link {\r\n        cursor: pointer;\r\n    }\r\n</style>\r\n\r\n<template>\r\n    <div>\r\n        <div>\r\n            <div class=\"card card-default\">\r\n                <div class=\"card-header\">\r\n                    <div style=\"display: flex; justify-content: space-between; align-items: center;\">\r\n                        <span>\r\n                            Personal Access Tokens\r\n                        </span>\r\n\r\n                        <a class=\"action-link\" tabindex=\"-1\" @click=\"showCreateTokenForm\">\r\n                            Create New Token\r\n                        </a>\r\n                    </div>\r\n                </div>\r\n\r\n                <div class=\"card-body\">\r\n                    <!-- No Tokens Notice -->\r\n                    <p class=\"mb-0\" v-if=\"tokens.length === 0\">\r\n                        You have not created any personal access tokens.\r\n                    </p>\r\n\r\n                    <!-- Personal Access Tokens -->\r\n                    <table class=\"table table-borderless mb-0\" v-if=\"tokens.length > 0\">\r\n                        <thead>\r\n                            <tr>\r\n                                <th>Name</th>\r\n                                <th></th>\r\n                            </tr>\r\n                        </thead>\r\n\r\n                        <tbody>\r\n                            <tr v-for=\"token in tokens\">\r\n                                <!-- Client Name -->\r\n                                <td style=\"vertical-align: middle;\">\r\n                                    {{ token.name }}\r\n                                </td>\r\n\r\n                                <!-- Delete Button -->\r\n                                <td style=\"vertical-align: middle;\">\r\n                                    <a class=\"action-link text-danger\" @click=\"revoke(token)\">\r\n                                        Delete\r\n                                    </a>\r\n                                </td>\r\n                            </tr>\r\n                        </tbody>\r\n                    </table>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Create Token Modal -->\r\n        <div class=\"modal fade\" id=\"modal-create-token\" tabindex=\"-1\" role=\"dialog\">\r\n            <div class=\"modal-dialog\">\r\n                <div class=\"modal-content\">\r\n                    <div class=\"modal-header\">\r\n                        <h4 class=\"modal-title\">\r\n                            Create Token\r\n                        </h4>\r\n\r\n                        <button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-hidden=\"true\">&times;</button>\r\n                    </div>\r\n\r\n                    <div class=\"modal-body\">\r\n                        <!-- Form Errors -->\r\n                        <div class=\"alert alert-danger\" v-if=\"form.errors.length > 0\">\r\n                            <p class=\"mb-0\"><strong>Whoops!</strong> Something went wrong!</p>\r\n                            <br>\r\n                            <ul>\r\n                                <li v-for=\"error in form.errors\">\r\n                                    {{ error }}\r\n                                </li>\r\n                            </ul>\r\n                        </div>\r\n\r\n                        <!-- Create Token Form -->\r\n                        <form role=\"form\" @submit.prevent=\"store\">\r\n                            <!-- Name -->\r\n                            <div class=\"form-group row\">\r\n                                <label class=\"col-md-4 col-form-label\">Name</label>\r\n\r\n                                <div class=\"col-md-6\">\r\n                                    <input id=\"create-token-name\" type=\"text\" class=\"form-control\" name=\"name\" v-model=\"form.name\">\r\n                                </div>\r\n                            </div>\r\n\r\n                            <!-- Scopes -->\r\n                            <div class=\"form-group row\" v-if=\"scopes.length > 0\">\r\n                                <label class=\"col-md-4 col-form-label\">Scopes</label>\r\n\r\n                                <div class=\"col-md-6\">\r\n                                    <div v-for=\"scope in scopes\">\r\n                                        <div class=\"checkbox\">\r\n                                            <label>\r\n                                                <input type=\"checkbox\"\r\n                                                    @click=\"toggleScope(scope.id)\"\r\n                                                    :checked=\"scopeIsAssigned(scope.id)\">\r\n\r\n                                                    {{ scope.id }}\r\n                                            </label>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                        </form>\r\n                    </div>\r\n\r\n                    <!-- Modal Actions -->\r\n                    <div class=\"modal-footer\">\r\n                        <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Close</button>\r\n\r\n                        <button type=\"button\" class=\"btn btn-primary\" @click=\"store\">\r\n                            Create\r\n                        </button>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Access Token Modal -->\r\n        <div class=\"modal fade\" id=\"modal-access-token\" tabindex=\"-1\" role=\"dialog\">\r\n            <div class=\"modal-dialog\">\r\n                <div class=\"modal-content\">\r\n                    <div class=\"modal-header\">\r\n                        <h4 class=\"modal-title\">\r\n                            Personal Access Token\r\n                        </h4>\r\n\r\n                        <button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-hidden=\"true\">&times;</button>\r\n                    </div>\r\n\r\n                    <div class=\"modal-body\">\r\n                        <p>\r\n                            Here is your new personal access token. This is the only time it will be shown so don't lose it!\r\n                            You may now use this token to make API requests.\r\n                        </p>\r\n\r\n                        <textarea class=\"form-control\" rows=\"10\">{{ accessToken }}</textarea>\r\n                    </div>\r\n\r\n                    <!-- Modal Actions -->\r\n                    <div class=\"modal-footer\">\r\n                        <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Close</button>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    export default {\r\n        /*\r\n         * The component's data.\r\n         */\r\n        data() {\r\n            return {\r\n                accessToken: null,\r\n\r\n                tokens: [],\r\n                scopes: [],\r\n\r\n                form: {\r\n                    name: '',\r\n                    scopes: [],\r\n                    errors: []\r\n                }\r\n            };\r\n        },\r\n\r\n        /**\r\n         * Prepare the component (Vue 1.x).\r\n         */\r\n        ready() {\r\n            this.prepareComponent();\r\n        },\r\n\r\n        /**\r\n         * Prepare the component (Vue 2.x).\r\n         */\r\n        mounted() {\r\n            this.prepareComponent();\r\n        },\r\n\r\n        methods: {\r\n            /**\r\n             * Prepare the component.\r\n             */\r\n            prepareComponent() {\r\n                this.getTokens();\r\n                this.getScopes();\r\n\r\n                $('#modal-create-token').on('shown.bs.modal', () => {\r\n                    $('#create-token-name').focus();\r\n                });\r\n            },\r\n\r\n            /**\r\n             * Get all of the personal access tokens for the user.\r\n             */\r\n            getTokens() {\r\n                axios.get('/oauth/personal-access-tokens')\r\n                        .then(response => {\r\n                            this.tokens = response.data;\r\n                        });\r\n            },\r\n\r\n            /**\r\n             * Get all of the available scopes.\r\n             */\r\n            getScopes() {\r\n                axios.get('/oauth/scopes')\r\n                        .then(response => {\r\n                            this.scopes = response.data;\r\n                        });\r\n            },\r\n\r\n            /**\r\n             * Show the form for creating new tokens.\r\n             */\r\n            showCreateTokenForm() {\r\n                $('#modal-create-token').modal('show');\r\n            },\r\n\r\n            /**\r\n             * Create a new personal access token.\r\n             */\r\n            store() {\r\n                this.accessToken = null;\r\n\r\n                this.form.errors = [];\r\n\r\n                axios.post('/oauth/personal-access-tokens', this.form)\r\n                        .then(response => {\r\n                            this.form.name = '';\r\n                            this.form.scopes = [];\r\n                            this.form.errors = [];\r\n\r\n                            this.tokens.push(response.data.token);\r\n\r\n                            this.showAccessToken(response.data.accessToken);\r\n                        })\r\n                        .catch(error => {\r\n                            if (typeof error.response.data === 'object') {\r\n                                this.form.errors = _.flatten(_.toArray(error.response.data.errors));\r\n                            } else {\r\n                                this.form.errors = ['Something went wrong. Please try again.'];\r\n                            }\r\n                        });\r\n            },\r\n\r\n            /**\r\n             * Toggle the given scope in the list of assigned scopes.\r\n             */\r\n            toggleScope(scope) {\r\n                if (this.scopeIsAssigned(scope)) {\r\n                    this.form.scopes = _.reject(this.form.scopes, s => s == scope);\r\n                } else {\r\n                    this.form.scopes.push(scope);\r\n                }\r\n            },\r\n\r\n            /**\r\n             * Determine if the given scope has been assigned to the token.\r\n             */\r\n            scopeIsAssigned(scope) {\r\n                return _.indexOf(this.form.scopes, scope) >= 0;\r\n            },\r\n\r\n            /**\r\n             * Show the given access token to the user.\r\n             */\r\n            showAccessToken(accessToken) {\r\n                $('#modal-create-token').modal('hide');\r\n\r\n                this.accessToken = accessToken;\r\n\r\n                $('#modal-access-token').modal('show');\r\n            },\r\n\r\n            /**\r\n             * Revoke the given token.\r\n             */\r\n            revoke(token) {\r\n                axios.delete('/oauth/personal-access-tokens/' + token.id)\r\n                        .then(response => {\r\n                            this.getTokens();\r\n                        });\r\n            }\r\n        }\r\n    }\r\n</script>\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
